import React, { useCallback, useEffect, useMemo } from "react";
import { getFileToKeyMap } from "./maps/fileToKey.map";
import { getKeyName } from "./maps/keyCodeToKey.map";
import PianoKey from "./PianoKey";

import "./index.css";

const fileToKeyMap = getFileToKeyMap();
// 500ms
const extendTime = 1000;

const intervals = new Map();

function getAudioElement (keyName: string): HTMLAudioElement | null {
    return document.getElementById(`pianoKey${keyName}`) as HTMLAudioElement;
}

function createAudioFadeInterval (audio: HTMLAudioElement, extendTime: number) {
    let _extendTime = extendTime;
    const timer = setInterval(() => {
        const { volume } = audio;
        if (_extendTime < 10 || volume < 0.03) {
            clearInterval(timer);
        } else if (volume < 0.6) {
            audio.volume = volume - 0.01;
        } else {
            audio.volume = volume * 0.95;
        }
        _extendTime -= 10;
    }, 100);
    return timer;
}

export default function Piano () {
    const keyDownListener = useCallback(
        function (ev: React.KeyboardEvent<HTMLDivElement>): void {
            const keyName = getKeyName(ev.code);
            if (keyName !== undefined && !fileToKeyMap[keyName].isPlay) {
                const audio = getAudioElement(keyName);
                if (audio !== null) {
                    fileToKeyMap[keyName].isPlay = true;
                    audio.pause();
                    audio.volume = 1;
                    audio.loop = false;
                    audio.currentTime = 0;
                    audio.play().then(v => {
                        audio.onended = function () {
                            fileToKeyMap[keyName].isPlay = false;
                            audio.pause();
                        };
                    }, r => { alert(r); });
                } else {
                    alert("can not find audio Element in document");
                }
            }
        }, []);

    const keyUpListener = useCallback(
        function (ev: React.KeyboardEvent<HTMLDivElement>): void {
            const keyName = getKeyName(ev.code);
            if (keyName !== undefined && fileToKeyMap[keyName].isPlay) {
                fileToKeyMap[keyName].isPlay = false;
            }
            console.log("keydown");
        }, []);

    const audioList = useMemo(() => {
        return Object.entries(fileToKeyMap).map((m, index) => {
            return <audio
                onPlay={(e) => {
                    clearInterval(intervals.get(m));
                    intervals.set(m, createAudioFadeInterval(e.currentTarget, extendTime));
                }} key={index} id={`pianoKey${m[0]}`}>
                <source src={`/audios/piano/mp3/${m[1].url}.mp3`} />
                <source src={`/audios/piano/ogg/${m[1].url}.ogg`} />
            </audio>;
        });
    }, []);

    const pianoKeys = useMemo(() => {
        const whiteKeyWidth = 40;
        const blackKeyWidth = 20;
        let currentPosition = 0;
        return Object.entries(fileToKeyMap).map((m, index) => {
            const isWhiteKey = !m[0].includes("#");
            const width = isWhiteKey ? whiteKeyWidth : blackKeyWidth;
            const left = currentPosition;
            currentPosition += width;
            return <PianoKey
                key={index}
                name={m[0]}
                listenKeyCode={getKeyName(m[0])}
                isWhiteKey={isWhiteKey}
                style={{
                    width: `${width}px`,
                    height: "100px",
                    left: `${left}px`,
                    top: "0"
                }} />;
        });
    }, []);

    return (
        <div>
            <div>
                {audioList}
            </div>
            <div className="piano">
                {pianoKeys}
            </div>
        </div>
    );
}
