import React, { useState } from "react";

import "./index.css";

interface PianoKeyProps {
    name: string
    listenKeyCode?: string
    isWhiteKey: boolean
    style: {
        width: string
        height: string
        left: string
        top: string
    }
};

export default function PianoKey (props: PianoKeyProps) {
    const { name, listenKeyCode, isWhiteKey, style } = props;
    const [isPressing, setIsPressing] = useState(false);

    function handleKeyDown (ev: React.KeyboardEvent<HTMLDivElement>): void {
        if (ev.code === listenKeyCode) {
            setIsPressing(true);
        }
        console.log(ev.code, listenKeyCode);
    }

    function handleKeyUp (ev: React.KeyboardEvent<HTMLDivElement>): void {
        if (ev.code === listenKeyCode) {
            setIsPressing(false);
        }
    }

    return (
        <div
            onKeyDown={handleKeyDown}
            onKeyUp={handleKeyUp}
            className={`pianoKey ${isWhiteKey ? "white" : "black"} ${isPressing ? ".press" : ""}`}
            style={{ ...style }}>
            <span>{name}</span>
        </div>
    );
}
